@import url(https://fonts.googleapis.com/css?family=Archivo:400,500,600,700);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #F4F4F4;
}

html, body {
    max-width: 100%;
    overflow-x: hidden;
}
.AllTracks {
  padding: 20px 70px;
}

.AllTracks_TrackList {
  width: 100%;
}

.TrackList_HeaderCell:hover {
  cursor: pointer;
  color: #222;
}

.TrackList_Row:hover {
  background-color: #eee;
}

.AllTracks_TrackList table {
  border-collapse: collapse;
}

.AllTracks_TrackList th {
  text-align: left;
  padding-bottom: 10px;
  padding-top: 10px;
  font-weight: 400;
  color: #BBB;
  font-size: 13px;
  letter-spacing: 0.1em;
}

.AllTracks_TrackList td {
  padding: 12px 0px;
  padding-right: 20px;
  list-style-type: none;
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  border-top: solid 1px #D8D8D8;
  border-collapse: collapse;
  max-width: 150px;
}

.SearchBar {
  border: none;
  width: 100%;
  box-sizing: border-box;
  padding: 10px 15px;
  background: transparent;
  font-size: 14px;
  border-radius: 3px;
  margin: 10px 0px;
}

.SearchBar:focus {
  outline: none;
  background-color: rgba(0, 0, 0, 0.04);
}

.PlaylistHeader_Name {
  color: #95A4D6;
  text-align: center;
  font-size: 45px;
  font-weight: 700;
  margin-bottom: 0px;
  margin-top: 20px;
}

.PlaylistSubHeader_Name {
  text-align: center;
  font-weight: 600;
  font-size: 18px;
  margin-bottom: 50px;
}

.PlaylistHeader_Subtitle {
  text-align: center;
  font-weight: normal;
  font-size: 15px;
  margin-bottom: 30px;
}

.Bold {
  font-weight: bold;
  font-size: 12px;
}

.Spinner {
  height: 50px;
  margin-top: 20%;
}

.centered {
  text-align: center;
}

@media only screen and (max-width: 900px) {
  .TrackList_HeaderCell_Album {
    display: none;
  }

  .Tracklist_Album {
    display: none;
  }
}

@media only screen and (max-width: 700px) {
  .TrackList_HeaderCell_Artist {
    display: none;
  }

  .Tracklist_Artist {
    display: none;
  }

  .PlaylistHeader_Name {
    font-size: 30px;
  }

  .PlaylistSubHeader_Name {
    font-size: 13px;
  }
}
.Header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  padding: 12px;
}

.Header_Img {
  height: 35px;
  border-radius: 30px;
}

.Header_Name {
  margin-left: 10px;
  margin-right: 15px;
}

.Header_Logout {
  margin-left: 10px;
  margin-right: 20px;
  background: #95A4D6;
  border-radius: 100px;
  border: none;
  padding: 10px 20px;
  color: #fff;
  -webkit-transition: background-color 0.5s;
  transition: background-color 0.5s;
}

.Header_Logout:hover {
  cursor: pointer;
  background: #8594C4;
}
.Home {
  color: #444444;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#FAFAFA), to(#F0F0F0));
  background-image: -webkit-linear-gradient(top, #FAFAFA 0%, #F0F0F0 100%);
  background-image: linear-gradient(-180deg, #FAFAFA 0%, #F0F0F0 100%);
  height: 100%;
  background-attachment: fixed;
}

.Home_Main_Content {
  margin-left: 250px;
}
.Landing {
  position: fixed;
  height: 100%;
  width: 100%;
  text-align: center;
  padding-top: 120px;
  color: #ffffff;
  background-image: -webkit-linear-gradient(206deg, #B873AC 5%, #8BB2E2 100%);
  background-image: linear-gradient(-116deg, #B873AC 5%, #8BB2E2 100%);
}

.Landing_Title_Playlist_Analyzer {
  font-size: 50px;
}

.Landing_Title_Spotify {
  font-size: 40px;
  font-weight: normal;
}

.Landing_Title_Web_App {
  font-size: 20px;
  font-weight: normal;
}

.Landing_Title_Name {
  font-weight: 600;
  color: #eee;
}

.Landing_Button_Login {
  width: 200px;
  height: 70px;
  background-color: #2DC760;
  border-radius: 100px;
  border: none;
  color: #fff;
  font-size: 16px;
  margin-top: 20px;
  -webkit-transition: background-color 0.5s;
  transition: background-color 0.5s;
}

.Landing_Button_Login:hover {
  cursor: pointer;
  background-color: #2FD566;
}

.Landing_Button_Login:focus {
  outline: none;
}
.App {
  text-align: center;
  margin: 0px;
  padding: 0px;
  border: 0px;
  
}

* {
  font-family: 'Archivo', sans-serif;
}
